
#gallery ul{
  white-space: nowrap;
}
#gallery ul li{
  padding-left: 15px;
  width: 180px;
}
#gallery ul li p{
  color: #a9ab5c;
}
#main{
  position: fixed;
  width: 100%;
  color: #fff;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
#gallery, #modal{
  max-width: 768px;
  margin: 0 auto;
}
#modal{
  z-index: 1075;
  position: fixed;
  height: 100vh;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: none;
}
.modal-dialog{
  position: relative;
  max-width: 500px;
  margin: 0 auto;
}
.modal-content{
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0,0,0,.2);
  border-radius: .3rem;
  outline: 0;
}
.modal-body{
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  padding: 15px;
  color: #333;
  font-size: 14px;
}
.modal-footer{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  /*justify-content: flex-end;*/
  padding: 15px;
  border-top: 1px solid #e9ecef;
  background-color: #171721;
}
.footer-div{
  height: 60px;
  width: 45%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: 20px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  /*float: left;*/
  background: url(../assets/background.png) no-repeat center -5px;
  text-decoration: none;
  text-shadow: 1px 1px 0px #000;
  border-radius: 15px;
  text-align: center;
  text-decoration: none;
}
.footer-div .link{
  font-size: 14px;
  color: #fff;
}
.footer-right{
  float: right;
}
.modal-backdrop{
  position: fixed;
  height: 100vh;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1040;
  display: none;
  background-color: #000;
}
.act{
  font-size: 18px;
  line-height: 2em;
}
.actor__content--typing::after {
  content: '|';
  -webkit-animation: blink 500ms infinite;
          animation: blink 500ms infinite;
  vertical-align: top;
  font-size: 24px;
  line-height: 2em;
}
.actor__content::after {
  content: '|';
  -webkit-animation: blink 500ms infinite;
          animation: blink 500ms infinite;
  /*vertical-align: inherit;*/
  font-size: 24px;
}
@-webkit-keyframes blink {
from { opacity: 0;
}
to { opacity: 1;
}
}
@keyframes blink {
from { opacity: 0;
}
to { opacity: 1;
}
}
#eg2, #eg3{
  background: url(../assets/background.png) no-repeat center -105px;
  display: block;
  opacity: 0;
  width: 230px;
  margin: 0;
  padding: 20px 15px;
  text-align: center;
  font-family: Arial, sans-serif;
  font-size: 18px;
  letter-spacing: 0.1em;
  color: #fff;
  text-decoration: none;
  text-shadow: 1px 1px 0px #000;
  border-radius: 15px;
  background: url(../assets/stripes.png) no-repeat 0px 0px, url(../assets/background.png) no-repeat center -8px;
  background-size: cover;
  -webkit-box-shadow: 2px 2px 10px #000;
          box-shadow: 2px 2px 10px #000;
  -webkit-transition: all 1s ease-out;
  transition: all 1s ease-out;
  -webkit-animation: 1s linear 0s infinite alternate ho;
          animation: 1s linear 0s infinite alternate ho;
  -webkit-box-shadow: 4px 4px 5px #1a3a2c;
          box-shadow: 4px 4px 5px #1a3a2c;
}
#eg2:hover{
  background: url(../assets/background.png) no-repeat center -5px;
  background: url(../assets/stripes.png) no-repeat 0px -200px, url(../assets/background.png) no-repeat center -8px;
  background-size: cover;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
#eg3{
  margin: 0 auto;
}
@-webkit-keyframes ho {
100%{
    background: url(../assets/linear-gradient.png) no-repeat 0px 0px, url(../assets/background.png) no-repeat center -8px;
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
    background-size: cover;
}
}
@keyframes ho {
100%{
    background: url(../assets/linear-gradient.png) no-repeat 0px 0px, url(../assets/background.png) no-repeat center -8px;
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
    background-size: cover;
}
}
.show{
  opacity: 0.9;
}
h1,
h2,
p {
  font-weight: normal;
  background-color: #222932;
  max-width: 768px;
  margin: 0 auto;
  padding:0 15px;
}
#c{
  margin-bottom: 10px;
  max-width: 768px;
  text-align: justify;
}
p{
  color: #666;
}
h1 {
  letter-spacing: 0.01em;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  line-height: 1.5em;
  /*border-top: 2px solid #181e26;*/
}



